    from {
        -webkit-transform: rotate(0);
        transform: rotate(0)
    }

    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg)
    }
}

.tfw-view-icon {
    display: inline-block;
    transition: all .3s;
    margin: 2px;
    padding: 0;
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}

